        margin-right: 5px; } }

.spinner-animation {
  margin: 0 auto;
  width: 40px;
  height: 40px;
  position: absolute;
  left: calc(50% - 20px);
  top: calc(50% - 20px);
  text-align: center;
  animation: sk-rotate 2.0s infinite linear; }
  .spinner-animation .dot1, .spinner-animation .dot2 {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: #333;
    border-radius: 100%;
    animation: sk-bounce 2.0s infinite ease-in-out; }
  .spinner-animation .dot2 {
    top: auto;
    bottom: 0;
    animation-delay: -1.0s; }

@keyframes sk-rotate {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg); } }

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0); }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1); } }

