            .navigation .mobile-nav .nav-mobile-opened .rodal-dialog .nav-item .sub-nav-menu .dropdown-item:active {
              border: none;
              transition: 0.2s ease-in-out;
              background-color: white !important; }
            .navigation .mobile-nav .nav-mobile-opened .rodal-dialog .nav-item .sub-nav-menu .dropdown-item img {
