.phone-button-container {
  margin-right: 20px;
  padding: 16px 30px;
  border-radius: 50px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  color: white !important;
  display: flex;
  justify-content: center;
  align-items: center; }
  .phone-button-container img {
    margin-right: 10px; }
  @media screen and (max-width: 576px) {
    .phone-button-container {
      margin-right: 10px;
      padding: 8px 10px;
      font-size: 12px; }
      .phone-button-container img {
        width: 10px;
        height: 10px;
        margin-right: 5px; } }
