.speech-scroll-2 .list-popup-container {
  padding-top: 40px;
  transform: translateX(20px);
  position: fixed;
  z-index: 999;
  transition: min-height 0.3s ease;
  transition: opacity 0.3s ease;
  opacity: 1; }
  .speech-scroll-2 .list-popup-container .list-popup-background {
    position: fixed;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.3); }
    .speech-scroll-2 .list-popup-container .list-popup-background .arrow {
      position: absolute;
      filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
      content: url(../../../assets/images/forms/triangle.svg);
      right: calc(50% - 10px);
      margin-top: -9px;
      z-index: 30; }
  .speech-scroll-2 .list-popup-container .list-popup {
    padding: 12px 20px; }
    .speech-scroll-2 .list-popup-container .list-popup button {
      display: flex;
      align-items: center;
      width: 100%;
      transition: background-color 0.3s ease; }
      .speech-scroll-2 .list-popup-container .list-popup button:hover, .speech-scroll-2 .list-popup-container .list-popup button:active {
        text-decoration: none !important; }
      .speech-scroll-2 .list-popup-container .list-popup button:hover {
        background-color: #F2FAF9;
        border-radius: 5px; }
      .speech-scroll-2 .list-popup-container .list-popup button .insurer-container {
        margin: 16px 30px;
        padding: 0; }
      .speech-scroll-2 .list-popup-container .list-popup button p {
        font-size: 16px;
        font-weight: 600;
        color: #46737A;
        margin: 0; }
      .speech-scroll-2 .list-popup-container .list-popup button:active {
        background-color: #99BBC1; }

.speech-scroll-2 .list-popup-container-closed {
  min-height: 0;
  opacity: 0;
  pointer-events: none; }

.speech-scroll-2 .speech-scroll-clickable:hover {
  cursor: pointer; }
