.speech-scroll .list-popup-container {
  padding-top: 40px;
  position: fixed;
  z-index: 1500;
  transition: min-height 0.3s ease, opacity 0.3s ease;
  opacity: 1;
  width: 282px;
  overflow: hidden; }
  .speech-scroll .list-popup-container .list-popup-background {
    position: fixed;
    transform: translateX(20px);
    width: 262px;
    height: 273px;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.3); }
    .speech-scroll .list-popup-container .list-popup-background .arrow {
      position: absolute;
      filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
      content: url(../../../assets/images/forms/triangle.svg);
      right: calc(50% - 10px);
      margin-top: -9px;
      z-index: 30; }
  .speech-scroll .list-popup-container .scrollbar-container {
    position: absolute;
    width: 6px;
    right: 2px;
    bottom: 2px;
    top: 42px;
    border-radius: 3px;
    overflow: hidden;
    z-index: 99; }
    .speech-scroll .list-popup-container .scrollbar-container .scrollbar {
      position: relative;
      display: block;
      width: 100%;
      cursor: pointer;
      border-radius: inherit;
      background-color: rgba(0, 0, 0, 0.2);
      height: 30px;
      transform: translateY(0px); }
  .speech-scroll .list-popup-container .list-popup {
    width: calc(100% + 17px);
    padding: 12px 27px 12px 30px;
    position: absolute;
    overflow-y: scroll; }
    .speech-scroll .list-popup-container .list-popup button {
      display: flex;
      align-items: center;
      width: 100%;
      transition: background-color 0.3s ease; }
      .speech-scroll .list-popup-container .list-popup button:hover, .speech-scroll .list-popup-container .list-popup button:active {
        text-decoration: none !important; }
      .speech-scroll .list-popup-container .list-popup button:hover {
        background-color: #F2FAF9;
        border-radius: 5px; }
      .speech-scroll .list-popup-container .list-popup button .insurer-container {
        margin: 16px 30px;
        padding: 0;
        width: 30px;
        height: 30px; }
      .speech-scroll .list-popup-container .list-popup button p {
        font-size: 16px;
        font-weight: 600;
        color: #46737A;
        margin: 0; }
      .speech-scroll .list-popup-container .list-popup button .popup-discount {
        position: absolute;
        left: 5px;
        width: 45px;
        height: 30px;
        border-radius: 4px;
        padding: 2px 8px;
        background-image: linear-gradient(236deg, #ffdf00, #ff9f0b);
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
        display: flex;
        flex-direction: column; }
        .speech-scroll .list-popup-container .list-popup button .popup-discount p {
          font-size: 14px;
          font-weight: bold;
          color: white;
          margin: 0;
          letter-spacing: -0.3px;
          line-height: 10px; }
        .speech-scroll .list-popup-container .list-popup button .popup-discount .up {
          font-size: 6px;
          text-align: left; }
        .speech-scroll .list-popup-container .list-popup button .popup-discount .bottom {
          display: flex; }
          .speech-scroll .list-popup-container .list-popup button .popup-discount .bottom .bottom-left {
            text-align: start;
            font-size: 18px; }
          .speech-scroll .list-popup-container .list-popup button .popup-discount .bottom .bottom-right {
            margin-left: 1px; }
            .speech-scroll .list-popup-container .list-popup button .popup-discount .bottom .bottom-right .percent {
              font-size: 11px;
              line-height: 6px; }
            .speech-scroll .list-popup-container .list-popup button .popup-discount .bottom .bottom-right .off {
              font-size: 6px;
              line-height: 10px; }
      .speech-scroll .list-popup-container .list-popup button:active {
        background-color: #99BBC1; }
        .speech-scroll .list-popup-container .list-popup button:active .popup-discount {
          left: -25px; }

.speech-scroll .list-popup-container-closed {
  min-height: 0;
  opacity: 0;
  pointer-events: none; }

.speech-scroll .speech-scroll-clickable:hover {
  cursor: pointer; }
